import { Value } from '@ali/4ever-cangjie';
import isEmptyParagraph from './isEmptyParagraph';

export const isEmptyValue = (value: Value): boolean => {
  const { document } = value;
  if (document.nodes.length === 0) return true;
  const first = document.nodes[0];
  return document.nodes.length === 1
    && isEmptyParagraph(first);
};
